/* eslint-disable */

import '../App.scss';
import React, { Component } from 'react';
import { Button } from 'react-bootstrap';

function Upload() {

    return (
        <div className="upload">
            <h4>상품 올리기</h4>
            <div className="form">
                <form action="/upload" method="POST">
                    <div className="input">
                        <hr />
                        <input type="text" placeholder='글 제목' name='title' />
                        <hr />
                        <select name="" id="" name='category'>
                            <option value="" selected disabled>카테고리 선택</option>
                            <option value="디지털기기">디지털기기</option>
                            <option value="생활가전">생활가전</option>
                            <option value="가구/인테리어">가구/인테리어</option>
                            <option value="유아동">유아동</option>
                            <option value="생활/가공식품">생활/가공식품</option>
                            <option value="유아도서">유아도서</option>
                            <option value="스포츠/레저">스포츠/레저</option>
                            <option value="여성잡화">여성잡화</option>
                            <option value="여성의류">여성의류</option>
                            <option value="남성패션/잡화">남성패션/잡화</option>
                            <option value="게임/취미">게임/취미</option>
                            <option value="뷰티/미용">뷰티/미용</option>
                            <option value="반려동물용품">반려동물용품</option>
                            <option value="도서/티켓/음반">도서/티켓/음반</option>
                            <option value="식물">식물</option>
                            <option value="기타 중고물품">기타 중고물품</option>
                        </select>
                        <hr />
                        <input type="text" placeholder='가격' name='price' />
                        <hr />
                        <textarea name="text" id="" cols="30" rows="10" placeholder='게시글 내용을 작성해주세요'></textarea>
                    </div>
                    <Button variant="outline-secondary" className='btn' type="submit">상품 올리기</Button>
                </form>
            </div>
        </div>
    )
}
export default Upload;